@import "ScssHelpers/_helpers_all.scss";

.body--popup_open {
	overflow: hidden;
}

.contact_popup {
	@extend %popup;
}

// ------------------------------------------- background
.contact_popup__bg {
	@extend %stretch;

	background-image: $gradient;
	opacity: .85;
	z-index: -1;
}

// ------------------------------------------- in
.contact_popup__in {
	position: relative;

	width: 100%;

	background: $white;
	overflow-y: auto;

	@include bp($point_2, min) {
		max-width: 57.4rem;
		padding: 7.3rem 7rem;
	}

	@include bp($point_3, $point_7) {
		max-width: 36.6rem;
		padding: 5rem 4.8rem;
	}

	@include bp($point_7 - 1) {
		padding: 4rem 2rem;
	}

	&.contact_popup__in--height_mod {
		height: 100%;
	}
}

// ------------------------------------------- title
.contact_popup__title {
	font-weight: 800;
	letter-spacing: 0.008em;
	text-align: center;
	text-transform: uppercase;
	line-height: 1.06;
	color: $black;

	@include bp($point_2, min) {
		margin-bottom: 5.5rem;

		font-size: 3rem;
	}

	@include bp($point_3) {
		margin-bottom: 4rem;

		font-size: 2.1rem;
	}

	&.contact_popup__title--padding_mod {
		margin-bottom: 1.7rem;
	}

	&.contact_popup__title--complete {
		margin-bottom: 2.5rem;
		line-height: 1.07;
	}
}

.contact_popup__descr {
	max-width: 90%;
	margin: 0 auto;

	font-weight: 300;
	letter-spacing: 0.016em;
	line-height: 1.2;
	text-align: center;
	color: $gray_3;

	@include bp($point_2, min) {
		font-size: 2.3rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}


.contact_form_message {
	margin: 0 auto;
	max-width: 36rem;
	font-size: 2.3rem;
	font-weight: 300;
	letter-spacing: 0.023rem;
	line-height: 1.09;
	text-align: center;

	@include bp($point_4) {
		font-size: 1.6rem;
		max-width: 23rem;
	}
}

// ------------------------------------------ button close
.contact_popup__btn_close {
	@extend %transition_opacity;

	position: absolute;
	top: 0;
	right: 0;

	cursor: pointer;

	border-radius: 50%;

	@include bp($point_2, min) {
		padding: 2.8rem 3rem;
	}

	@include bp($point_3) {
		padding: 1.8rem;
	}

	&:hover {
		opacity: .5;
	}
}

.contact_popup__btn_close_icon {
	@extend %transition_color;

	display: block;

	color: $black;

	@include bp($point_2, min) {
		width: 1.3rem;
		height: 1.3rem;
	}

	@include bp($point_3) {
		width: 1rem;
		height: 1rem;
	}
}

.contact_form__btn_wrap {
	text-align: center;
}

.contact_form__btn {
	@extend %dark_btn_1;
	@extend %btn_size_3;

	line-height: 1.33;
	border: 0;

	@include bp($point_3) {
		padding-top: 0 !important;
	}
}
