@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;
	font-family: $font_1;

	cursor: pointer;

	&:hover, &:focus {
		text-decoration: none;
	}
}

//color extends
%dark_btn_1 {
	@extend %transition_color_background;

	font-weight: 800;
	color: $white;
	-webkit-text-fill-color: $white;
	background-color: $gray_3;
	// border: 1px solid transparent;
	box-shadow: inset 0 0 0 1px $gray_3;

	@include st_btn();

	&:hover {
		background-color: $white_3;
		color: $gray_3;
		-webkit-text-fill-color: $gray_3;
		// border-color: $gray_3;
	}
}

%light_btn_1 {
	@extend %transition_color_background;

	font-weight: 800;
	color: $black_2;
	background-color: $white_3;
	border: 1px solid transparent;

	@include st_btn();

	&:hover {
		background-color: $gray_3;
		color: $white;
		border-color: $white_3;
	}
}

//size extends
%btn_size_1 {
	-webkit-appearance:none;

	@include bp($point_2, min) {
		height: 7.4rem;
		line-height: 7.4rem;

		padding: 0 4.8rem;

		font-size: 2.3rem;

		border-radius: $gl_radius;

		.body--safari_state & {
			padding-top: 1.2rem;
		}
	}

	@include bp($point_3) {
		height: 3.5rem;
		line-height: 3.5rem;

		padding: 0 3.3rem;

		font-size: 1.6rem;

		border-radius: 3.2rem;

	}
}

%btn_size_2 {
	-webkit-appearance:none;

	@include bp($point_2, min) {
		height: 7.4rem;
		line-height: 7.4rem;

		padding: 0 7.3rem;

		font-size: 2.3rem;

		border-radius: $gl_radius;

	}

	@include bp($point_3) {
		height: 5.2rem;
		line-height: 5.2rem;

		padding: 0 5.1rem;

		font-size: 1.6rem;

		border-radius: 2.6rem;
	}
}

%btn_size_3 {
	-webkit-appearance:none;
	padding: 0 2rem;

	@include bp($point_2, min) {
		height: 5rem;
		line-height: 5rem;

		min-width: 20rem;

		font-size: 1.8rem;

		border-radius: $gl_radius;

	}

	@include bp($point_3) {
		height: 3.5rem;
		line-height: 3.5rem;

		min-width: 12rem;

		font-size: 1.5rem;
		font-weight: 400;

		border-radius: $gl_radius;
	}
}
