@import "ScssHelpers/_helpers_all.scss";

.contact_section {
	@include bp($point_2,min) {
		padding: 1.6rem var(--gl-indent) 6rem;
	}

	@include bp($point_3, $point_7) {
		padding: 2.4rem var(--gl-indent) 6.1rem;
	}
}

// ------------------------------------------- section in
.contact_section__in {
	position: relative;
	margin: 0 auto;
	
	@include bp($point_2,min) {
		z-index: 101; // fix header overlay
	}
	
	.contact_section__row--bottom_mod & {
		@include bp($point_3, $point_6) {
			max-width: 72%;
		}
	}
}

.contact_section__row {
	position: relative;

	@include bp($point_2,min) {
		margin-right: calc(-1 * var(--gl-indent));
	}

	&.contact_section__row--top_mod {
		@include bp($point_2, min) {
			padding: 4.2rem 4rem 8.8rem 8rem;
		}

		@include bp($point_3) {
			padding: 5.5rem 0 12.7rem;
		}
	}

	&.contact_section__row--bottom_mod {
		@include bp($point_2, min) {
			padding: 6.5rem 2rem 11.7rem;

			background: $gradient;
		}

		@include bp($point_3) {
			padding: 7.5rem 0 8rem;
		}
	}
}

// -------------------------------------------- background
.contact_section__bg_w {
	@extend %stretch;

	overflow: hidden;
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		margin: 0 calc(-1 * var(--gl-indent)) 0 0;
	}

	@include bp($point_3) {
		width: 100vw;
		margin: 0 calc(-1 * var(--gl-indent)) 0 calc(-1 * var(--gl-indent));
	}

	.contact_section__row--bottom_mod & {
		background: $gradient_2;
	}
}

.contact_section__bg {
	height: 100%;
	width: 100%;

	.contact_section__row--top_mod & {
		fill: $white_2;
		@include bp($point_3) {
			svg {
				width: 100%;
				height: 100%;
			}
		}
	}

	.contact_section__row--bottom_mod & {
		color: $gray_5;

		@include bp($point_2, min) {
			width: 100%;
		}

		@include bp($point_3) {
			width: 126rem;
			max-width: 100%;
		}
	}
}

.contact_section__bg_desk {
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		display: block;

		.body--safari_state & {
			opacity: 0;
		}
	}
	@include bp($point_3) {
		display: none;
	}

	@include bp(1920px, min) {
		.contact_section__row--top_mod & {
			position: relative;
			top: -8rem;
			transform: scale(1.39);
		}

	}

}

.contact_section__bg_mob {
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		display: none;
	}
	@include bp($point_3) {
		display: block
	}
}

.contact_section__bg_img {
	@extend %cover_img;
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	@include bp($point_2, min) {
		.body--safari_state & {
			opacity: 1;
		}
	}
}

// ------------------------------------------- header
.contact_section__header {
	@include bp($point_2, min) {
    padding: 0 2rem 0 16.5rem;
	}

	@include bp($point_3) {
		text-align: center;
	}
}

// ------------------------------------------- title
.contact_section__title {
	letter-spacing: 0.01em;
	font-weight: 800;
	text-transform: uppercase;
	color: $black;

	@include bp($point_2, min) {
		margin-bottom: 6.6rem;

		font-size: 7rem;
		line-height: 1.03;

		br {
			display: none;
		}
	}

	@include bp($point_3) {
		margin: 0 -.4rem 3.4rem;

		font-size: 4rem;
		line-height: 1.08;

		br {
			display: block;
		}
	}
}

// ------------------------------------------- contacts
.contact_section__contacts {
	@include bp($point_7, min) {
		max-width: 44.6rem;
		margin-left: auto;
	}

	@include bp($point_7 - 1) {
		margin: 0 .2rem;
	}
}

// ------------------------------------------- socials
.contact_section__socials_block {
	margin: 0 0 2.7rem 0;
}

.contact_section__socials_list {
	@extend %global_flex_block_row_wrap_flex-start;

	margin: 0 -1.1rem -1.1rem 0;
}

.contact_section__socials_item {
	padding: 0 1.1rem 1.1rem 0;
}

.contact_section__socials_link {
	@extend %transition_color_background;

	display: flex;

	color: $white;
	background: transparent;
	border-radius: 50%;
	border: 1px solid $white;

	@include bp($point_2, min) {
		width: 3rem;
		height: 3rem;
	}

	@include bp($point_3) {
		width: 2.5rem;
		height: 2.5rem;
	}

	&:hover {
		color: $black;
		background: $white;
	}
}

.contact_section__socials_icon {
	display: block;
	margin: auto;

	@include bp($point_2, min) {
		width: 1.1rem;
		height: 1.1rem;
	}

	@include bp($point_3) {
		width: .9rem;
		height: .9rem;
	}
}

// ------------------------------------------- address
.contact_section__address_title {

	font-weight: 800;
	line-height: 1;
	letter-spacing: 0.006em;
	text-transform: uppercase;
	color: $white;

	@include bp($point_2, min) {
		font-size: 3.8rem;
	}

	@include bp($point_3) {
		margin-bottom: .2rem;

		font-size: 2.4rem;
	}
}

.contact_section__address {
	font-weight: 300;
	line-height: 1.3;
	letter-spacing: 0.006em;
	color: $white;


	@include bp($point_2, min) {
		font-size: 2.4rem;
	}

	@include bp($point_3) {
		font-size: 2.1rem;
	}
}
