@import "ScssHelpers/_helpers_all.scss";

.contact_form {
	.form_input__element:not([type='submit']),
	.form_textarea__element {
		letter-spacing: 0.01em;
		
		border-radius: 1rem;
		background-color: #f3f3f3;
		border: none;
		box-shadow: none;

		@include bp($point_3) {
			height: 2.9rem;
			padding: 0 1.2rem;

			font-size: 1.6rem;
			letter-spacing: -0.06em;

			border-radius: .7rem;
		}
	}

	.form_textarea__element {
		padding-top: .5rem;
		padding-bottom: .5rem;

		@include bp($point_2, min) {
			height: 9.8rem;

			letter-spacing: 0.01em;
		}

		@include bp($point_3) {
			height: 6.8rem;
			padding-top: .1rem;

			border-radius: .7rem;
		}
	}

	// from rows
	.form_row:not(.form_row--last_mod):not(.form_row--btn_mod) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}

		@include bp($point_3) {
			margin-bottom: 1.3rem;
		}
	}

	.form_row--last_mod {
		@include bp($point_2, min) {
			margin-bottom: 4.6rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}

	.form_row--two_mod {
		@extend %global_flex_block_row_wrap_flex-start;

		@include bp($point_2, min) {
			margin-right: -2rem;
		}

		@include bp($point_3) {
			margin-right: -1rem;
		}

		.form_input {
			@include bp($point_2, min) {
				width: 50%;
				padding-right: 2rem;
			}

			@include bp($point_3, $point_7) {
				width: 50%;
				padding-right: 1rem;
			}

			@include bp($point_7 - 1) {
				width: 100%;
				padding-right: 1rem;

				 &:not(:last-child) {
					 padding-bottom: 1rem;
				 }
			}
		}
	}

	.form_row--btn_mod {
		@extend %global_flex_block_row_wrap_center;

		.form_input__element {
			@extend %dark_btn_1;
			@extend %btn_size_3;
		}
	}
}
